:root {
  font-size: 14px; }

@media only screen and (min-width: 768px) {
  :root {
    font-size: 18px; } }

body {
  overflow-x: hidden;
  font-family: 'Source Sans Pro', sans-serif; }

section h2 {
  text-transform: uppercase;
  padding-top: 3%;
  text-align: center;
  color: white;
  font-weight: 700;
  font-size: 1.8rem;
  width: 100%; }

.navbar {
  background-color: white;
  text-transform: uppercase; }
  .navbar__logo {
    font-family: 'Knewave', cursive;
    font-size: 2rem;
    color: #5b509c; }
    .navbar__logo:hover, .navbar__logo:focus {
      text-decoration: none; }

.navbar-nav .nav-link {
  color: #5b509c; }

.navbar li a:hover {
  border: 1px solid #5b509c;
  border-radius: 290486px; }

.navbar li:active {
  background-color: #5b509c;
  border-radius: 290486px;
  transition: all .3s; }
  .navbar li:active a {
    color: white !important; }

header {
  min-height: 94vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 0; }

.welcome h2 {
  color: #293347;
  font-weight: 700;
  font-size: 3rem; }

.welcome p {
  margin-top: 2%;
  text-align: center;
  width: 100%;
  font-size: 1.4rem; }

.down {
  align-self: flex-end;
  text-align: center;
  width: 100%;
  text-transform: uppercase;
  font-weight: 700; }

@media only screen and (min-width: 768px) {
  header {
    min-height: 99vh; } }

.about-me {
  position: relative;
  min-height: 99vh;
  margin-top: 0;
  background-color: #5b509c;
  color: white;
  padding-bottom: 3.5rem; }
  .about-me p {
    line-height: 2rem;
    text-align: center;
    font-size: 1.5rem;
    padding: 2rem; }

.services {
  min-height: 80vh; }
  .services h2 {
    margin: 0;
    color: #293347; }
  .services p {
    text-align: center;
    color: #293347; }

.portfolio {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center; }

.website {
  position: relative;
  margin: 10px;
  object-fit: none;
  border-radius: 23px;
  transition: all .3s; }
  .website:hover {
    box-shadow: 0 0 6px #5b509c;
    transform: scale(1.05); }
  .website:active {
    box-shadow: none;
    transform: scale(1); }
  .website img {
    height: 100%;
    border-radius: 23px;
    filter: brightness(75%); }
  .website span {
    text-align: center;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    color: white;
    line-height: 1000%;
    font-size: 1.3rem;
    font-weight: 700;
    text-transform: uppercase;
    transition: all .3s; }
    .website span:hover {
      background-color: rgba(0, 0, 0, 0);
      cursor: pointer; }

.skills {
  position: relative;
  display: flex;
  justify-content: center; }
  .skills .container {
    margin-top: -6.5rem;
    background-color: white;
    bottom: 10%;
    padding: 40px; }
  .skills .card {
    text-align: center;
    color: #5b509c; }
    .skills .card h2 {
      text-transform: uppercase;
      padding: 10px;
      font-weight: 700;
      color: #293347; }
    .skills .card i {
      font-size: 2.5rem;
      color: #5b509c;
      padding: 20px; }

.contact {
  padding: 10px 0px;
  padding-bottom: 2.5rem; }
  .contact form {
    font-family: 'roboto', sans-serif;
    width: 460px;
    margin: 40px auto 40px;
    box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.2);
    padding: 0 0 40px;
    border-radius: 3px;
    color: #293347; }
  .contact input::-webkit-input-placeholder,
  .contact textarea::-webkit-input-placeholder {
    font: inherit;
    transition: transform 0.3s ease-in-out, font-size 0.3s ease-in-out, visibility 0.3s ease-in-out; }
  .contact input,
  .contact textarea {
    font: inherit;
    font-size: 0.8em;
    margin: 28px 25px 10px;
    width: 400px;
    display: block;
    border: none;
    padding: 20px 0 10px;
    border-bottom: solid 1px #5b509c;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 96%, #5b509c 4%);
    background-position: -400px 0;
    background-size: 400px 100%;
    background-repeat: no-repeat;
    transition: background 0.3s cubic-bezier(0.64, 0.09, 0.08, 1);
    resize: none;
    overflow: hidden; }
    .contact input:focus::-webkit-input-placeholder,
    .contact textarea:focus::-webkit-input-placeholder {
      color: #5b509c; }
    .contact input:focus, .contact input:valid,
    .contact textarea:focus,
    .contact textarea:valid {
      box-shadow: none;
      outline: none;
      background-position: 0 0; }
      .contact input:focus::-webkit-input-placeholder, .contact input:valid::-webkit-input-placeholder,
      .contact textarea:focus::-webkit-input-placeholder,
      .contact textarea:valid::-webkit-input-placeholder {
        font-size: 0.8em;
        transform: translateY(-20px);
        visibility: visible !important;
        opacity: 1; }
  .contact input[type="email"] {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 96%, #5b509c 4%);
    background-position: -400px 0;
    background-repeat: no-repeat;
    transition: border-bottom 0.3s ease, background 0.3s ease; }
    .contact input[type="email"] ~ span.validation-text {
      position: absolute;
      visibility: hidden;
      font-family: 'roboto', sans-serif;
      font-size: 0.6em;
      width: 400px;
      margin-left: 25px;
      margin-top: -5px;
      color: white;
      transition: color 0.3s ease-in-out; }
    .contact input[type="email"]:not([value=""])::-webkit-input-placeholder {
      font-size: 0.8em;
      transform: translateY(-20px);
      visibility: visible !important;
      opacity: 1; }
    .contact input[type="email"]:not([value=""]):focus::-webkit-input-placeholder {
      color: #5b509c; }
    .contact input[type="email"]:not([value=""]):focus:not(:valid)::-webkit-input-placeholder {
      color: #E91E63; }
    .contact input[type="email"]:focus {
      background-position: 0 0; }
      .contact input[type="email"]:focus:not(:valid):not([value=""]) {
        border-bottom: solid 1px #E91E63;
        background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 96%, #E91E63 4%); }
    .contact input[type="email"]:not(:focus):not(:valid):not([value=""]) {
      border-bottom: solid 1px #E91E63;
      background-position: 0 0;
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 96%, #E91E63 4%); }
      .contact input[type="email"]:not(:focus):not(:valid):not([value=""])::-webkit-input-placeholder {
        color: #E91E63; }
      .contact input[type="email"]:not(:focus):not(:valid):not([value=""]) ~ span.validation-text {
        visibility: visible;
        color: #E91E63; }
  .contact .flex {
    display: flex;
    align-items: stretch; }
    .contact .flex textarea {
      line-height: 120%; }
  .contact button {
    font-family: 'roboto', sans-serif;
    transition: transform 0.2s cubic-bezier(0.64, 0.09, 0.08, 1), box-shadow 0.2s cubic-bezier(0.64, 0.09, 0.08, 1);
    border: none;
    background: #5b509c;
    cursor: pointer;
    border-radius: 3px;
    padding: 6px;
    width: 400px;
    color: white;
    margin: 30px 0 0 25px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.2); }
    .contact button:hover {
      transform: translateY(-2px);
      box-shadow: 0 5px 6px 0 rgba(0, 0, 0, 0.2); }

.contact h2 {
  color: #293347;
  margin-bottom: 0; }

